.wd-bg-yellow {
    background-color: lightyellow;
}

.wd-bg-blue {
    background-color: lightblue;
}

.wd-bg-red {
    background-color: lightcoral;
}

.wd-fg-black {
    color: black;
}

.wd-padding-10px {
    padding: 10px
}

.wd-tabs li {
    display: inline;
}

.wd-tabs {
    padding-left: 0px;
}

.wd-selected {
    border-bottom: 2px blue solid;
    padding-bottom: 20px;
}

.wd-nudge-up {
    bottom: 18px;
    left: 25px;
}

.wd-bottom-2 {
    bottom: 2px;
}

.wd-bottom-4 {
    bottom: 4px;
}



.wd-topic{
    font-size: 13px;
    color: rgb(110, 118, 125);
}

li.post{
    font-size: 13px;
    font-weight: bold !important;
}

div.container{
    border: black;
}

.wd-post-topic{
    font-size: medium;
    font-weight: bold !important;
}

.wd-display-pic {
    border-radius: 50% !important;
    width: 50px !important;
    height: 50px !important;
    float: left !important;
}


.ul.li.nav-tabs.wd-active {
    border-bottom-color: dodgerblue;
}

.wd-info {
    color: darkgray;
}

.card#post {
    border-width: thin;
    border-color: lightsteelblue;
    border-radius: 12px;
}
